.i{
    font-family: 'Poppins', sans-serif;
    display: flex;
    height: 100vh;
    justify-content: center;
    align-items: center;
    padding-right: 5vw;
}

.i-left{
    padding-left: 5vw;
    flex: 2;
    align-content: center;
    justify-content: center;
}

.i-right{
    flex: 1; 
    position: relative;
}

.i-left-wrapper{
    height: 50%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.i-intro{
    font-size: 30px;
    font-weight: 300;
}

.i-name{
    font-size: 60px;
}

.i-title{
    height: 50px;
    overflow: hidden;
}

.i-title-item{
    height: 50px;
    font-size: 30px;
    font-weight: bold;
    color: #688eff;
    display: flex;
    align-items: center;
}

.i-title-wrapper{
    height: 100%;
    animation: move 10s ease-in-out infinite alternate;
}

@keyframes move{
    0%{
        transform: translateY(0);
    }
    50%{
        transform: translateY(-50px);
    }
    100%{
        transform: translateY(-100px);
    }
}

.i-img{
    width: 100%;
    height: 100%;
    object-fit: contain;
}

.i-bg{
    clip-path: polygon(25% 0, 100% 0, 100% 100%, 25% 100%, 0 50%);
    background-color: #ffffff;
}

@media screen and (max-width: 600px) {
    .i {
        flex-direction: column;
      }
    
      .i-left-wrapper {
      
        padding: 10px;
        align-items: center;
        justify-content: center;
        text-align: center;
        height: 100%;
      }
    
      .i-desc{
        display: none;
      }

      .i-title-item{
        font-size: 20px;
      }
}